<template>
  <div>
      <img class="headimg" src="..\assets\2c933500885db5db33cc5094480e9ae8.jpeg" alt="">
      <ul class="personitem">
        <li v-for="(item, index) in personList" :key="index" class="persontitle" @click="itemclick(item)">
          <span class="personi">{{ item.label }}</span>
          <i>></i>
        </li>
        
      </ul>
  </div>
</template>

<script>
import { Store } from 'vuex'
export default {
  data() {
    return {
      personList: [
        { label: '商品收藏' },
        { label: '我的足迹' },
        { label: '店铺收藏' },
        { label: '我的订单' },
        { label: '退出登录', type: 'exit'},
      ]
    }
  },
  methods: {
    itemclick(item) {
      if(item.type == 'exit') {
        this.$store.commit('settoken', '')
        localStorage.removeItem('token')
        this.$router.push({ path: '/login' })
      }
    }
  }
}
</script>

<style lang="stylus">
  .persontitle
    font-size 18px
    text-align left
    height 50px
    padding-left 5%
    border-bottom 1px solid #eee
    line-height 50px
  .personi
    display inline-block
    width 90%

  .headimg
    width 100%
    height 150px
</style>